

@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

#userDashTop{
    display: flex;
    flex-direction: row;
    padding-bottom: 4%;
    width: 110%;
    background-color: #EEE;
    margin-left: -3.7%;
    
}
#userDasNav{
    display: flex;
    flex-direction: row;

}

#userDasNav > .link1{
    margin-right: 15%;
    color: #01091d;
    font-family: 'Montserrat';
